<template>
  <div>
    <!-- 게시물 갯수만큼 반복 :step 0이면 보이는 영역 -->
    <div v-if="step == 0">
      <Post :post="post[i]" v-for="(a, i) in post" :key="i" :idx="i" />
    </div>
    <!-- 반복문 사용 :post="post[i]" v-for="(a, i) in post" :key="i" -->

    <!-- 필터 선택 페이지 :step 1이면 보이는 영역 -->
    <div v-if="step == 1">
      <div
        class="upload-image"
        :style="`background-image:url(${imageS})`"
      ></div>
      <div class="filters">
        <div class="filter-1"></div>
        <div class="filter-1"></div>
        <div class="filter-1"></div>
        <div class="filter-1"></div>
        <div class="filter-1"></div>
      </div>
    </div>
  </div>

  <!-- 글작성 페이지 :step 2이면 보이는 영역-->
  <div v-if="step == 2">
    <div class="upload-image" :style="`background-image:url(${imageS})`"></div>
    <div class="write">
      <textarea
        class="write-box"
        @input="$emit('write', $event.target.value)"
      ></textarea>
    </div>
  </div>
</template>

<script>
import Post from "./Post.vue";

export default {
  name: "Container",
  data() {
    return {};
  },
  components: {
    Post: Post,
  },
  props: {
    post: Array,
    step: Number,
    imageS: String,
  },
};
</script>

<style>
div {
  background: #313339;
}
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 1px;
  margin: auto;
  display: block;
  outline: none;
}
</style>
