<template>
  <div class="header">
    <ul class="header-button-left">
      <li>cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 0" @click="step++">Next</li>
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" alt="logo" />
  </div>
  <h4>안녕 {{ $store.state.name }}{{ $store.state.age }}</h4>
  <button @click="$store.commit('이름변경')">이름변경</button>
  <button @click="$store.commit('plus')">age</button>

  <Container
    :imageS="imageS"
    :post="post"
    :step="step"
    @write="작성한글 = $event"
  />

  <button @click="more">더보기</button>
  <div class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload"
        multiple
        accept="image/*"
        type="file"
        id="file"
        class="inputfile"
      />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

  <div v-if="step == 0">내용 0</div>
  <div v-if="step == 1">내용 1</div>
  <div v-if="step == 2">내용 2</div>
  <button @click="step = 0">버튼 0</button>
  <button @click="step = 1">버튼 1</button>
  <button @click="step = 2">버튼 2</button>
</template>
<script>
import Container from "./components/Container.vue";
import postdata from "./assets/postdata.js";
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      post: postdata,
      더보기: 0,
      step: 0,
      imageS: "",
      작성한글: "", // 유저가 작성할 글공간 비우기
    };
  },
  components: {
    Container: Container,
  },
  methods: {
    more() {
      axios
        .get(`https://jellyzoo.github.io/data/vuemore${this.더보기}.json`)
        .then((결과) => {
          this.post.push(결과.data);
          this.$store.commit("changePosts", this.post);
          this.더보기++;
        });
    },
    upload(e) {
      const 파일 = e.target.files; // 업로드한 파일들이 담겨있는곳
      console.log(파일[0]); // 업로드한 파일을 콘솔에 출력
      const url = URL.createObjectURL(파일[0]); // 함수를 이용해 URL생성
      console.log(url);
      this.imageS = url; // imageS라는 변수에 url을 담아
      this.step++;
    },
    publish() {
      const 내게시물 = {
        name: "Kim Hyun",
        userImage:
          "https://img0.yna.co.kr/photo/cms/2019/09/19/19/PCM20190919000219505_P2.jpg",
        postImage: this.imageS,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.작성한글,
        filter: "perpetua",
      };
      this.post.unshift(내게시물);
      this.step = 0;
    },
  },
};
</script>
